}

.search-form {
  margin-top: 20px;
}

.search-form__input {
  border: 1px solid var(--border-color);
  color: var(--font-color);
  box-sizing: border-box;
  border-radius: 4px;
  width: 100%;
  font-family: inherit;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search-form__input--big {
  background: url("Search.ccd3c07b.svg") 26px / 19px no-repeat;
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
}

.search-form__input--small {
  background: url("Search.ccd3c07b.svg") 13px / 11px no-repeat;
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
}

.search-form__input:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.search-form__input:focus {
  border-radius: 3px;
  outline: none;
  font-weight: 800;
}

.search-form__input--big:focus {
  background: url("Search.ccd3c07b.svg") 26px no-repeat;
}

.search-form__input--small:focus {
  color: var(--font-color);
